.about-container {
  padding: 4rem 2rem;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  max-width: 900px;
  margin: 0 auto;
  text-align: left;
}

.about-title {
  font-size: 2.5rem;
  color: #007BFF;
  text-align: center;
  margin-bottom: 2rem;
  font-weight: bold;
}

.about-section {
  margin-bottom: 2.5rem;
}

.section-subtitle {
  font-size: 1.8rem;
  color: #0056b3;
  margin-bottom: 1rem;
  font-weight: bold;
}

.about-content {
  font-size: 1.2rem;
  color: #555;
  line-height: 1.8;
  margin-bottom: 1.5rem;
  text-align: justify;
}

.about-content strong {
  color: #007BFF;
}

.about-motto {
  text-align: center;
}

.motto {
  font-size: 1.6rem;
  font-style: italic;
  color: #333;
  margin: 2rem 0;
  padding: 1rem;
  border-left: 5px solid #007BFF;
  background-color: #fff;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);
}
