/* Glavni kontejner za motivacionu sekciju */
.motivation-container {
  padding: 4rem 2rem;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  background-color: #f9f9f9;  /* Svetla pozadina za bolji kontrast */
  border-radius: 10px;  /* Blagi zaobljeni uglovi */
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);  /* Blaga senka za efekat */
}

/* Naslov motivacione sekcije */
.motivation-title {
  font-size: 2.5rem;
  color: #007BFF;
  margin-bottom: 1.5rem;
  font-weight: bold;
  letter-spacing: 1.2px;  /* Dodaj razmak između slova za stilizaciju */
}

/* Glavni sadržaj */
.motivation-content {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 2rem;
  line-height: 1.8;
  text-align: justify;  /* Justifikovani tekst za profesionalniji izgled */
}

/* Stil za pasuse */
.motivation-container p {
  margin-bottom: 1.5rem;
}

/* Stil za citat */
.motivation-quote {
  font-style: italic;
  color: #333;
  font-size: 1.4rem;
  margin-bottom: 2rem;
  padding: 1rem;
  background-color: #fff;
  border-left: 5px solid #007BFF;  /* Istaknut citat sa obojenom linijom */
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);
}

/* Responsive dizajn */
@media (max-width: 768px) {
  .motivation-title {
    font-size: 2rem;  /* Smanji veličinu naslova na manjim ekranima */
  }

  .motivation-content {
    font-size: 1.1rem;  /* Smanji veličinu teksta za mobilne uređaje */
  }

  .motivation-quote {
    font-size: 1.2rem;  /* Smanji veličinu citata */
  }
}
