
/* Container styling */
.approach-container {
  padding: 1.5rem;
  border-left: 5px solid #007BFF;
  background-color: #f4f8fb;
  margin: 2rem auto;
  max-width: 800px;
  text-align: center;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

/* Text styling */
.approach-text {
  font-size: 1.5rem;
  font-family: 'Playfair Display', serif;
  font-style: italic;
  color: #2C3E50;
  line-height: 1.6;
  display: inline-block;
  position: relative;
}

/* Quote marks styling */
.quote-mark {
  font-size: 2rem;
  color: #007BFF;
}


.home-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f0f4f8;
  padding: 2rem;
}

.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  padding: 3rem;
  width: 100%;
  max-width: 1200px;
}

.hero-content {
  flex: 1;
  padding-right: 2rem;
}

.hero-title {
  font-size: 3rem;
  color: #333;
  margin-bottom: 1rem;
  font-weight: bold;
}

.hero-subtitle {
  font-size: 1.4rem;
  color: #666;
  line-height: 1.8;
  margin-bottom: 2rem;
}

.hero-subtitle .highlight {
  color: #007BFF;
  font-weight: 600;
}

.skills-icons {
  display: flex;
  justify-content: space-around;
  margin: 2rem 0;
}

.skills-icons svg {
  margin: 0 1rem;
  color: #007BFF;
  transition: transform 0.3s ease;
}

.skills-icons svg:hover {
  transform: scale(1.1);
  color: #0056b3;
}

.cta-button {
  padding: 0.8rem 2rem;
  background-color: #007BFF;
  color: white;
  font-size: 1.2rem;
  border-radius: 50px;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #0056b3;
}

.hero-image img {
  width: 400px;
  height: auto;
  border-radius: 50%;
  box-shadow: 0px 8px 25px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}

.hero-image img:hover {
  transform: scale(1.05);
}

/* Media Queries for smaller screens */
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
    text-align: center;
    padding: 2rem;
  }

  .hero-content {
    padding: 0;
    margin-bottom: 2rem;
  }

  .hero-title {
    font-size: 2rem;  /* Manji naslov za mobilne uređaje */
  }

  .hero-subtitle {
    font-size: 1.2rem;  /* Manji podnaslov za mobilne uređaje */
  }

  .skills-icons {
    justify-content: center;
    flex-wrap: wrap;  /* Ikonice u više redova */
  }

  .skills-icons svg {
    margin: 0.5rem;  /* Manji razmak između ikonica */
  }

  .cta-button {
    font-size: 1rem;  /* Manja veličina dugmeta */
    padding: 0.7rem 1.5rem;
  }

  .hero-image img {
    width: 250px;  /* Manja slika za mobilne uređaje */
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 1.8rem;  /* Još manji naslov za vrlo male ekrane */
  }

  .hero-subtitle {
    font-size: 1rem;  /* Još manji podnaslov za vrlo male ekrane */
  }

  .hero-image img {
    width: 200px;  /* Još manja slika za vrlo male ekrane */
  }
}
