.projects-container {
  padding: 4rem 2rem;
  background-color: #f9f9f9;
  text-align: center;
}

.projects-heading {
  font-size: 2.5rem;
  color: #007BFF;
  margin-bottom: 2rem;
  font-family: 'Playfair Display', serif;
  font-weight: bold;
}

.project-item {
  background-color: #fff;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-item:hover {
  transform: translateY(-5px);
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
}

.project-title {
  font-size: 1.5rem;
  color: #333;
  margin-top: 1rem;
}

.project-description {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 1.5rem;
}

.project-link {
  color: #007BFF;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
}

.project-link:hover {
  color: #0056b3;
  text-decoration: underline;
}

/* Stil za ikonice */
.project-icon {
  font-size: 2.5rem;
  color: #007BFF;
  margin-bottom: 1rem;
}

/* Responsivni dizajn za manje ekrane */
@media (max-width: 768px) {
  .projects-container {
    padding: 3rem 1rem;
  }

  .project-item {
    padding: 1.5rem;
  }

  .projects-heading {
    font-size: 2rem;
  }

  .project-title {
    font-size: 1.3rem;
  }

  .project-description {
    font-size: 1rem;
  }

  .project-icon {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .projects-container {
    padding: 2rem 1rem;
  }

  .project-item {
    padding: 1rem;
  }

  .projects-heading {
    font-size: 1.8rem;
  }

  .project-title {
    font-size: 1.2rem;
  }

  .project-description {
    font-size: 0.9rem;
  }

  .project-icon {
    font-size: 1.8rem;
  }

  .project-link {
    font-size: 1rem;
  }
}
