/* Stilizacija za header */
.navbar-header {
  background-color: #f8f9fa;
  padding: 2rem 1rem;
  text-align: center;
  border-bottom: 2px solid #C0A080;
  position: relative;
}

.navbar-main-title {
  font-size: 3rem;
  color: #2C3E50;
  font-family: 'Playfair Display', serif;
  font-weight: bold;
  margin: 0;
  letter-spacing: 1.2px;
  text-transform: capitalize;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
  opacity: 0.95;
  
  /* Animation styles */
  position: relative;
  animation: slideIn 2s ease-out, fadeIn 2s ease-in;
}

/* Styling for the icon */
.navbar-title-icon {
  color: #FFD700; /* Gold color for the icon */
  margin-right: 10px; /* Space between the icon and the text */
  font-size: 2.5rem; /* Adjust icon size */
}

/* Slide-in and fade-in effect */
@keyframes slideIn {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.95;
  }
}


/* Container for date and time */
.navbar-top-info {
  position: absolute;
  top: 10px;
  right: 20px;
  padding: 10px 15px;
  font-size: 1.2rem;
  color: #fff;
  background-color: #2C3E50; /* Dark background for contrast */
  border-radius: 8px; /* Smooth rounded corners */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15); /* Subtle shadow */
  transition: background-color 0.3s ease, box-shadow 0.3s ease; /* Smooth transitions */
}

/* Styling for the date and time text */
.navbar-datetime {
  font-weight: bold;
  color: #FFD700; /* Elegant gold color for emphasis */
  letter-spacing: 1px; /* Slightly spaced letters */
}

/* Hover effect for extra polish */
.navbar-top-info:hover {
  background-color: #34495E; /* Slightly lighter background on hover */
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2); /* More pronounced shadow on hover */
}


/* Navigacija */
.navbar {
  background-color: #34495E;
  padding: 1rem;
  display: flex;
  justify-content: center;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 2rem;
  margin: 0;
  padding: 0;
}

.navbar-links a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  transition: color 0.3s ease;
}

.navbar-links a:hover {
  color: #F4D03F;
}

/* Stil za ikone */
.navbar-links a svg {
  margin-right: 0.5rem;
  font-size: 1.4rem;
}

/* Responsive dizajn za manje ekrane */
@media (max-width: 768px) {
  .navbar-links {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .navbar-main-title {
    font-size: 2.4rem;
  }

  .navbar-links a {
    font-size: 1.4rem;
  }

  .navbar-links a svg {
    font-size: 1.6rem;
  }
}
