/* Glavni kontejner za kontakt sekciju */
.contact-container {
  padding: 4rem 2rem;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  background-color: #f9f9f9;  /* Svetla pozadina za kontrast */
  border-radius: 10px;  /* Blagi zaobljeni uglovi */
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);  /* Senka za efekat dubine */
}

/* Naslov sekcije */
.contact-title {
  font-size: 2.5rem;
  color: #007BFF;
  margin-bottom: 1rem;
  font-weight: bold;
}

/* Opis sekcije */
.contact-description {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 2rem;
  line-height: 1.6;
}

/* Kontakt informacije */
.contact-info p {
  font-size: 1.2rem;
  margin: 1rem 0;
}

.contact-info a {
  color: #007BFF;
  text-decoration: none;
  transition: color 0.3s ease, text-decoration 0.3s ease;
}

.contact-info a:hover {
  color: #0056b3;
  text-decoration: underline;
}

/* Footer sekcija */
.contact-footer {
  margin-top: 3rem;
  font-size: 1.2rem;
  color: #555;
  border-top: 1px solid #ddd;
  padding-top: 1.5rem;
}

/* Dodajmo formu za direktan kontakt (ako je koristiš) */
.contact-form {
  margin-top: 2rem;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 1rem;
  margin: 0.5rem 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
}

.contact-form input[type="submit"] {
  background-color: #007BFF;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.contact-form input[type="submit"]:hover {
  background-color: #0056b3;
}

/* Responsive dizajn */
@media (max-width: 768px) {
  .contact-title {
    font-size: 2rem;
  }

  .contact-description {
    font-size: 1.1rem;
  }

  .contact-footer {
    font-size: 1rem;
  }
}
