/* Glavni kontejner za sekciju Technologies */
.technologies-container {
  padding: 4rem 2rem;
  background-color: #f9f9f9;
  text-align: center;
}

/* Naslov sekcije */
.technologies-title {
  font-size: 2.5rem;
  color: #007BFF;
  margin-bottom: 1rem;
  font-weight: bold;
  letter-spacing: 1.2px;  /* Dodaj malo razmaka između slova */
}

/* Opis ispod naslova */
.technologies-description {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 3rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;  /* Poboljšaj čitljivost sa boljim razmakom između linija */
}

/* Grid za tehnologije */
.technologies-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 2rem;
  justify-items: center;
}

/* Pojedinačni elementi tehnologija */
.technology-item {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  max-width: 200px;
  text-align: center;
}

.technology-item:hover {
  transform: translateY(-5px);
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
}

.technology-item h3 {
  margin-top: 1rem;
  font-size: 1.2rem;
  color: #333;
  font-weight: bold;
}

.technology-item svg {
  font-size: 3rem;
  color: #007BFF;
  transition: color 0.3s ease, transform 0.3s ease;
}

.technology-item:hover svg {
  color: #0056b3;
  transform: scale(1.1);  /* Uvećanje ikona pri hoveru */
}

/* Media Queries za manje ekrane */
@media (max-width: 768px) {
  .technologies-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }
  
  .technology-item {
    max-width: 150px;  /* Smanji maksimalnu širinu za mobilne uređaje */
  }
}

@media (max-width: 480px) {
  .technologies-title {
    font-size: 2rem;  /* Manji naslov za vrlo male ekrane */
  }

  .technologies-description {
    font-size: 1rem;
    margin-bottom: 2rem;  /* Smanji razmak ispod opisa */
  }

  .technology-item h3 {
    font-size: 1rem;  /* Manji font za naslove tehnologija */
  }

  .technology-item svg {
    font-size: 2.5rem;  /* Manje ikone za mobilne uređaje */
  }
}
